body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:url(https://images5.alphacoders.com/478/478568.jpg) no-repeat center center fixed;
  background-size: cover

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
	text-align: center;
	font-weight: 100;
	font-size: 3em;
	/*border: 1px solid black;*/
	margin: 10px;
	color: #ffa500;
}

.appLay {
	height: 100vh;
	width: auto;
	display: grid;
	grid-template-rows: auto auto 1fr;
}
.card {
	background: linear-gradient(to top left,rgba(234, 111, 55,0.7),rgba(128, 239, 236,0.7));
	text-align: center;
	display: inline-block;
	border-radius: 50px;
	margin: 10px;
	transition: 0.5s all;
	box-shadow: 0 0 15px rgb(0,0,0);
	color: black;
	font-weight: 100;
	font-size: 1.5em;
}

.card:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.card:active {
	-webkit-transform: scale(0.95);
	        transform: scale(0.95);
}
.cardplate {
	/*border: 2px solid black;*/
	text-align: center;
	/*height: 80vh;*/
	overflow-x: hidden;
	overflow-y: scroll;
	padding: 10px;
}
.input {
	text-align: center;
	margin: 10px;
	/*border: 1px solid black;*/
}

.input input {
	font-size: 1.3em;
	padding: 5px;
	background-color:rgba(165,217,236,0.5);
	border: 2px solid rgb(165,217,236);
	border-radius: 5px;
}
